.live-chart {
  width: 100%; }
  .live-chart h1 {
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    text-align: center;
    padding: 50px 10px;
    margin: 0; }
  .live-chart .chart {
    position: relative; }
  .live-chart .baseline {
    position: absolute;
    top: 0;
    left: 50%;
    width: 2px;
    height: 100%;
    z-index: 2;
    background: #555; }
    .live-chart .baseline span {
      position: absolute;
      left: 0;
      top: -40px;
      transform: translateX(-50%);
      font-style: italic; }
  .live-chart .chart-bars {
    position: relative;
    margin: 50px auto;
    height: 100%;
    width: 75%; }
    .live-chart .chart-bars.with-baseline .bar-wrapper {
      left: 50%; }
      .live-chart .chart-bars.with-baseline .bar-wrapper.behind-baseline {
        left: auto; }
  .live-chart .bar-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.5s ease; }
    .live-chart .bar-wrapper label {
      position: absolute;
      width: 200px;
      left: -210px;
      text-align: right;
      top: 50%;
      transform: translateY(-50%);
      font-size: 16px;
      font-weight: 700;
      display: flex;
      justify-content: flex-end;
      align-items: center; }
      .live-chart .bar-wrapper label img {
        max-height: 40px;
        margin-left: 10px; }
    .live-chart .bar-wrapper .value {
      position: absolute;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      font-size: 16px;
      font-weight: 700;
      margin-left: 10px; }
    .live-chart .bar-wrapper .bar {
      width: 100%; }
    .live-chart .bar-wrapper.behind-baseline {
      left: auto;
      right: 50%; }
      .live-chart .bar-wrapper.behind-baseline label {
        right: -210px;
        left: auto;
        text-align: left;
        flex-direction: row-reverse; }
        .live-chart .bar-wrapper.behind-baseline label img {
          margin: 0 10px 0 0; }
      .live-chart .bar-wrapper.behind-baseline .value {
        right: 100%;
        left: auto;
        margin-left: 0;
        margin-right: 10px; }
